import React, {  useRef, useEffect } from 'react'
import { useTrail, animated , useChain} from "react-spring/";
import {
  Hq_location as HqLocation, Date_aquired as DateAquired,
  Date_exited as DateExited, Logo,  Years_of_investment as YearsOfInvestment
} from './Nodes'
import  OtherLocations from './OtherLocations'
import  AddOnCompanies from './AddOnCompanies'
import VistaColors from '../Colors'

import { fireCustomGoogleTagEvent } from '../../gtm.js';

// REFERENCE DESIGN FOR THIS
let NODE = {
  logo: {
    dia: 2,
    x: .50, y: .90,
  },
  hq_location: {
    dia: 4.3,
    x: .85, y: .60,
  },
  date_exited: {
    dia: 1.95,
    x: .32, y: .40,
  },
  add_on_companies: {
    dia: 4,
    x: .40, y: .15,
  },
  other_locations: {
    dia: 4,
    x: .84, y: .22,
  },
  years_of_investment: {
    dia: 4,
    x: .1504, y: .5868,
  },
  // date_aquired:{
  //   x: .28, y: .75,
  // }

}
const a = (NODE.logo.x-NODE.years_of_investment.x)/2
const b = (NODE.logo.y-NODE.years_of_investment.y)/2
// const hyp = Math.sqrt(Math.pow(a, 2)+Math.pow(b,2))
NODE.date_aquired = {
//   // x: .2472, y: .6797,
  dia: 1.95,
  x: a+NODE.years_of_investment.x, y: b+NODE.years_of_investment.y,
}


const getSmallRandom = () => {
  return Math.random()*.1 
}

const Card = ( props ) => {
  
  let config = window.BrowserDetect.mobile? { duration: 200 }:   { mass: 2, tension: 2500, friction: 120 }
   
  const nodePlacement = {
    logo: {                x: `${100*(NODE.logo.x)}%`,                y: `${100*(NODE.logo.y)}%`},
    hq_location: {         x: `${100*(NODE.hq_location.x)}%`,         y: `${100*(NODE.hq_location.y)}%`},
    date_aquired: {        x: `${100*(NODE.date_aquired.x)}%`,        y: `${100*(NODE.date_aquired.y)}%`},
    date_exited: {         x: `${100*(NODE.date_exited.x)}%`,         y: `${100*(NODE.date_exited.y)}%`},
    add_on_companies: {    x: `${100*(NODE.add_on_companies.x)}%`,    y: `${100*(NODE.add_on_companies.y)}%`},
    other_locations: {     x: `${100*(NODE.other_locations.x)}%`,     y: `${100*(NODE.other_locations.y)}%`},
    years_of_investment: { x: `${100*(NODE.years_of_investment.x)}%`, y: `${100*(NODE.years_of_investment.y)}%`},
  }

  let nodeBubbles = ([
    <Logo key="Logo" src={props.company.logo} pos={nodePlacement.logo} radius={NODE.logo.dia} triggerTrainingMode={props.triggerTrainingMode}/>,
    <HqLocation key="Hq_location" loc={props.company.hq_location} pos={nodePlacement.hq_location} radius={NODE.hq_location.dia} triggerTrainingMode={props.triggerTrainingMode}/>,
    <DateAquired key="Date_aquired" year={props.company.date_aquired} pos={nodePlacement.date_aquired} radius={NODE.date_aquired.dia} triggerTrainingMode={props.triggerTrainingMode}/>,
    <YearsOfInvestment key="Years_of_investment" begining={props.company.date_aquired} end={props.company.date_exited} pos={nodePlacement.years_of_investment} radius={NODE.years_of_investment.dia} triggerTrainingMode={props.triggerTrainingMode}/>,
    <DateExited key="Date_exited" year={props.company.date_exited} pos={nodePlacement.date_exited} radius={NODE.date_exited.dia} triggerTrainingMode={props.triggerTrainingMode}/>,
    <OtherLocations key="Other_locations" list ={props.company.other_locations} pos={nodePlacement.other_locations} radius={NODE.other_locations.dia} toggleDetail={props.toggleDetail}/>,
    <AddOnCompanies key="Add_on_companies" list={props.company.add_on_companies} pos={nodePlacement.add_on_companies} radius={NODE.add_on_companies.dia} toggleDetail={props.toggleDetail}/>,
  ]); 

  let nodeLines = [
    {className:1,active: true,base:'hq_location',   x1: nodePlacement.logo.x, y1: nodePlacement.logo.y,  x2: nodePlacement.hq_location.x, y2: nodePlacement.hq_location.y },
    {className:1,active: true,base:'years_of_investment',   x1: nodePlacement.logo.x, y1: nodePlacement.logo.y,  x2: nodePlacement.years_of_investment.x, y2: nodePlacement.years_of_investment.y },
    {className:props.company.add_on_companies.length,active: props.company.add_on_companies.length>0,base:'add_on_companies', x1:nodePlacement.logo.x, y1: nodePlacement.logo.y, x2: nodePlacement.add_on_companies.x, y2:nodePlacement.add_on_companies.y},
    {className:props.company.other_locations.length,active: props.company.other_locations.length>0,base:'other_locations', x1:nodePlacement.hq_location.x, y1: nodePlacement.hq_location.y, x2: nodePlacement.other_locations.x, y2:nodePlacement.other_locations.y},
    {className:1,active: props.company.date_exited,base:'date_exited', x1:nodePlacement.years_of_investment.x, y1: nodePlacement.years_of_investment.y, x2: nodePlacement.date_exited.x, y2:nodePlacement.date_exited.y},
  ]

  let from = {  }
  let enter = { transform: "translate3d(0, 0, 0)" }
  if (props.onInitialCard) {
    from.transform = "translate3d(0, -1%, 0)"
  }
  else if (props.movingDirection === 'LEFT') {
    from.transform = "translate3d(-3%, 0 ,0)"
  }
  else if (props.movingDirection === 'RIGHT') {
    from.transform = "translate3d(3%, 0 ,0)"
  }
  

  const bubblesRef = useRef()

  const nodeBubblesTrail = useTrail(nodeBubbles.length, {
    config: config,
    from: from,
    to: enter,
    ref: bubblesRef
  })

  const linesRef = useRef();
  const nodeLinesTrail = useTrail(nodeLines.length, {
    config: config,
    from: from,
    to: enter,
    ref: linesRef
  })
  useChain([bubblesRef, linesRef], [0,0]);

  // console.log("\tCard", props.company.other_locations, props.company.add_on_companies,props.company.date_exited )
  useEffect(()=>{
    fireCustomGoogleTagEvent(1, props.company.name);
  }, [])
  return (
    <div className="content-wrapper">
      <div className="group-vert">
        <span className="group"> {props.company.group} </span>
        <span className="vert" style={{ color: VistaColors.verticals[props.company.industry_vertical] }}> {props.company.industry_vertical} </span>
      </div>

      <div className="name-copy">
        <h1 className="name" style={{ color: VistaColors.verticals[props.company.industry_vertical] }} dangerouslySetInnerHTML={{__html:props.company.name}}></h1>
        <p className="copy">
          {props.company.value_proposition}
        </p>
      </div>
      <svg id="nodes" className="nodes" >
        <g className="center">
        {nodeLinesTrail.map((style, index) => {
            if ( nodeLines[index].active ){
              return (
              <animated.line className={`nodeLines ${nodeLines[index].className>4?'':'dotted'}`} id={`line-to-${nodeLines[index].base}`}
                    x1={nodeLines[index].x1} y1={nodeLines[index].y1}
                    x2={nodeLines[index].x2} y2={nodeLines[index].y2}  key={index} style={style}/>
          )}
          else return null
        })}
        {nodeBubblesTrail.map((style, index) => (
          <animated.g id={nodeBubbles[index].key} style={style} key={index} >
              {nodeBubbles[index]}
            </animated.g>
        ))}
        </g>
      </svg>
    </div>
  )

}


export default Card;
