import React, { Component } from 'react'
import * as THREE from 'three'

import VistaColors from '../Colors'
import Stats from 'stats-js'
import {OrbitControls} from './lib/OrbitControlsVista.js'
import TWEEN from './lib/Tween.js'

import * as Geographer from './Geographer'
import Earth from './Earth'
import Loc from './Location'
import Cursor from './Cursor'
import ZoomBar from './ZoomBar'

import threads from 'threads'

//tmp: commenting this out because missing package, also not being used here
//import Ozai from './worker'

import './main.scss'
// https://daveceddia.com/useeffect-hook-examples/




const DEBUG = !true

const radius = 228
var geo = new THREE.SphereGeometry(3)
var mat = new THREE.MeshBasicMaterial({color: 0xff0000});
var mesh1 = new THREE.Mesh(geo, mat);
var mesh2 = new THREE.Mesh(geo, mat);

let shouldUpdateLocations = true
let origin = new THREE.Vector3(0, 0, 0)
let raycaster = new THREE.Raycaster( )

let zoomDirection = 0;
let lastZoom = 0;
let thresholds = {
              NEAR: 0.11,
              MID: 0.4,
              FAR: 0.55
            }

let zoomFlags = {
              NEAR: true,
              MID: true,
              FAR: true,
              DEFAULT: true
            }

const resetTexture = ( keep, textures ) => {
  for (var texture in textures) {
    if (textures.hasOwnProperty( texture )) {
      textures[texture].visible = false;
    }
  }

  textures[keep].visible = true;

}
const resetZoomFlags = ( keep ) => {
  for (var level in zoomFlags) {
    if (zoomFlags.hasOwnProperty( level )) {
      zoomFlags[level] = true
    }
  }
  zoomFlags[keep] = false
}
let round = ( num, numberDecimals )=>{
  let decimalCount = 1
  for (var i = 0; i < numberDecimals; i++) {
    decimalCount *= 10
  }
  if ( typeof num === "number") {
    return Math.round(num * decimalCount )/ decimalCount
  }
  else if (num instanceof Array ) {
    let tempArr = []
    for (var e = 0; e < num.length; e++) {
      tempArr[e] = Math.round(num[e] * decimalCount )/ decimalCount
    }
    return tempArr
  }
}


class Globe extends Component {
  constructor( props ) {
    super( props )
    this.locations = new Loc( { radius, origin } )
    this.cursor = { }
    this.state = {
      size: {},
      zoom: 80
    }
    this.lastActiveState = true

    // firstWorker = new Worker('worker.js')
    this.shouldRecalculate = true
    this.lastEventTimestamp = null

    this.firstLoad = true

    // instance props for click-to-rotate & camera tweening
    this.globeWasTapped = null;
    this.isCamTweening = false;
    this.camTweenDuration = 300;
    this.postCamTweenDelay = 300;
    this.center = new THREE.Vector3(0,0,0);

    // firstWorker = new Worker('worker.js')
  }

  componentDidMount( ) {
      this.sceneSetup( )
      this.addCustomSceneObjects( )
      this.startAnimationLoop( )
      window.addEventListener('resize', this.handleWindowResize)
      this.handleWindowResize( )
      this.firstLoad = false
      this.thread = threads.spawn(function(input, done) {
        // Everything we do here will be run in parallel in another execution context.
        // Remember that this function will be executed in the thread's context,
        // so you cannot reference any value of the surrounding code.
        if ( DEBUG ) console.log("webworker!",THREE)
        let THREEjs = THREE
        let raycaster = new THREEjs.Raycaster( )
        function getIntersectionWithEarth( _mouse ) {

          let mouse = {
            x: ((_mouse.x - 1) / input.clientWidth) * 2 - 1,
            y: -((_mouse.y - 1) / input.clientHeight) * 2 + 1
          }
          raycaster.setFromCamera(mouse, input.camera)

          return raycaster.intersectObject( input.object )
        }
        done({ a : getIntersectionWithEarth(input.a), b : getIntersectionWithEarth(input.b) });
      });
  }

  componentWillUnmount( ) {
      window.removeEventListener('resize', this.handleWindowResize)
      window.cancelAnimationFrame(this.requestID)
      this.controls.dispose( )
  }

  shouldComponentUpdate( ) {
    return true
  }

  // Standard scene setup in Three.js. Check "Creating a scene" manual for more information
  // https://threejs.org/docs/#manual/en/introduction/Creating-a-scene
  sceneSetup = ( ) => {
    if ( DEBUG ) console.log("Globe.sceneSetup")
      // get container dimensions and use them for scene sizing
      const width = this.mount.clientWidth
      const height = this.mount.clientHeight

      this.scene = new THREE.Scene( )
      this.camera = new THREE.PerspectiveCamera(
          45, // fov = field of view
          width / height, // aspect ratio
          0.1, // near plane
          20000 // far plane
      )
      this.camera.position.z = 600 // is used here to set some distance from a cube that is located at z = 0
      // OrbitControls allow a camera to orbit around the object
      // https://threejs.org/docs/#examples/controls/OrbitControls
      this.renderer = new THREE.WebGLRenderer({ antialias: true, alpha: true })
      this.renderer.setClearColor(new THREE.Color(VistaColors.general.Dark_Blue), 1)
      this.renderer.setSize( width, height )

      //event listener on canvas for double-clicking to rotate camera
      this.renderer.domElement.addEventListener('dblclick',this.clickToRotate.bind(this));
      this.renderer.domElement.addEventListener('touchstart', this.touchToRotate.bind(this));

      this.mount.appendChild( this.renderer.domElement ) // mount using React ref
      this.controls = new OrbitControls( this.camera, this.renderer.domElement )
      this.controls.enablePan = false
      this.controls.minDistance = radius * 1.2
      this.controls.maxDistance = radius * 3.6

      this.controls.addEventListener( "end", this.touchEnd.bind( this ) )
      this.controls.addEventListener("change", this.controlsChange.bind( this ) )

  }

  // Here should come custom code.
  // Code below is taken from Three.js BoxGeometry example
  // https://threejs.org/docs/#api/en/geometries/BoxGeometry
  addCustomSceneObjects = ( ) => {
    if ( DEBUG ) console.log("Globe.addCustomSceneObjects")
    // lights
    this.scene.add( new THREE.AmbientLight(0xFFFFFF) )

    // earth objects
    this.earth = new Earth( { radius, origin, color: VistaColors.general.Dark_Blue } )
    this.scene.add(this.earth.outline)
    this.scene.add(this.earth.sphere)
    this.scene.add(this.earth.earth)
    //temp
    if( DEBUG ){
      this.earth.earth.add(mesh1);
      this.earth.earth.add(mesh2);
    }
    // locations
    this.scene.add( this.locations.group )
  }

  startAnimationLoop = ( ) => {

    this.renderer.render( this.scene, this.camera )
    TWEEN.update( )
    this.controls.update( )

    this.lastActiveState = this.props.active
    this.requestID = window.requestAnimationFrame(this.startAnimationLoop)
  }

  stopAnimationLoop = ( ) => {
    window.cancelAnimationFrame(this.requestID)
    this.lastActiveState = this.props.active
  }

  touchEnd( e ) {

    if(this.isCamTweening) return; //allow camTween to handle

    const state = e.target.getState();

    if(state ===  -1 || state ===  3 ){
      if ( DEBUG ) console.log("Globe.touchend:zooming", e )
      window.clearTimeout(this.lastZoomTimeout)
      this.lastZoomTimeout = window.setTimeout( this.offloadWork.bind( this ) , 500)
    }
    else if(state ===  0 || state ===  4){//panning
      this.offloadWork( )
    }
  }

  clickToRotate( e ) {

		let intersects = this.getIntersectionWithEarth( e )

    if(intersects[0]) {
      let item    = intersects[0]
        , point   = item.point
        , camDist = this.camera.position.length( )
        , target  = new THREE.Vector3( )
        ;


      target.copy(item.point).normalize( ).multiplyScalar( camDist )
      this.tweenCamera( target )

      // note: this code sends the camera to position directly, with no tween
      //  this.camera.position.copy(point).normalize().multiplyScalar(camDist);
      //  this.controls.update();
    }
  }

  touchToRotate( e ) {

    let touch = {
      x : e.touches[0].clientX,
      y : e.touches[0].clientY
    };

    if(!this.globeWasTapped) {
      if(DEBUG) console.log('single-touch detected');
      this.globeWasTapped = setTimeout(()=>{
        this.globeWasTapped = null;
      }, 300);
    } else {
      if(DEBUG) console.log('double-touch detected');
      clearTimeout(this.globeWasTapped);
      this.globeWasTapped = null;
      this.clickToRotate(touch);
    }
  }

  tweenCamera(target) {

    this.isCamTweening = true;
    TWEEN.removeAll();

    let camTween = new TWEEN.Tween(this.camera.position)
      .to(target, this.camTweenDuration)
      .easing(TWEEN.Easing.Quadratic.InOut)
      .start()
      .onComplete(function() {
        setTimeout(function(){
          this.isCamTweening = false;
          this.offloadWork();
        }.bind(this), this.postCamTweenDelay);
      }.bind(this));
  }

  controlsChange( e ) {

    if(this.isCamTweening) return; //allow tweenCamera to handle

    const state = e.target.getState();

    if(state ===  -1 || state ===  4  ) {
      if ( DEBUG ) console.log(`Globe.controlsChange`, e)
      this.onZoom( e )
    }
    // 0 - panning
    // 4 zoming in touch
    // 3 panning in touch
  }

  onZoom( e )  {
    if( !window.BrowserDetect.mobile ){
      this.setState({ zoom: 100-round(this.controls.getZoomPercent( ) * 100, 3) })
    }
    let zoom = round(e.target.getZoomPercent( ),2)?round(e.target.getZoomPercent( ),2):0.01
    if ( zoom > lastZoom ) zoomDirection = -1
    else if ( zoom < lastZoom ) zoomDirection = 1

    this.earth.maps["countries"].mesh.material.opacity =  (Math.pow(zoom, 0.2)*0.3)+0.11
    this.earth.maps["usa_states"].mesh.material.opacity =  (Math.pow(1-zoom, 0.2)*0.3)+0.1

    if ( DEBUG ) console.log("\tonZoom", zoom , zoomDirection , this.camera.zoom )

    if(zoom < thresholds.NEAR &&  zoomFlags.NEAR ) {
      if ( DEBUG ) console.log("\n\tNEAR\n", zoom < thresholds.NEAR, zoomFlags.NEAR)
      this.locations.updateZoom("NEAR")
      this.controls.rotateSpeed = 0.15;
      resetTexture("NEAR", this.earth.worldMapTextures)
      resetZoomFlags("NEAR")
      console.log("ONLy ONCE")
    }
    else if(zoom > thresholds.NEAR && zoom < thresholds.MID &&  zoomFlags.MID ) {
      if ( DEBUG ) console.log("\n\tMID\n", zoom > thresholds.NEAR , zoom < thresholds.MID ,  zoomFlags.MID)
      this.locations.updateZoom("MID")
      this.controls.rotateSpeed = 0.5
      resetTexture("MID", this.earth.worldMapTextures)
      resetZoomFlags("MID")

    }
    else if (zoom > thresholds.MID && zoom < thresholds.FAR &&  zoomFlags.FAR ) {
      if ( DEBUG ) console.log("\n\tFAR\n", zoom > thresholds.MID , zoom < thresholds.FAR ,  zoomFlags.FAR )
      this.locations.updateZoom("FAR")
      this.controls.rotateSpeed = 0.8
      resetTexture("FAR", this.earth.worldMapTextures)
      resetZoomFlags("FAR")

    }
    else if ( zoom > thresholds.FAR && zoomFlags.DEFAULT ) {
      if ( DEBUG ) console.log("\n\tDEFAULT\n", zoom > thresholds.FAR , zoomFlags.DEFAULT)
      this.controls.rotateSpeed = 1
      this.locations.updateZoom("DEFAULT")
      resetTexture("DEFAULT", this.earth.worldMapTextures)
      resetZoomFlags("DEFAULT")

    }
    lastZoom = zoom

  }
  offloadWork( ){
    if( window.Worker && false ){
      let firstWorker = new Worker('worker.js')
      let obj = { }
      Object.assign( obj, { task:"getIntersectionWithEarth", params:{_mouse:this.cursor.position, _mouse2:this.cursor.edge, camera: this.camera, object:this.earth.sphere , raycaster}} )
      firstWorker.postMessage(  obj )
      console.log('Message posted to worker', firstWorker)

      firstWorker.onmessage = function( e ) {
        console.log('Message received from worker', e )
        this.getTargetLatLong(e.data[0], e.data[1] )
      }
      firstWorker.onerror = function( e ) {
        console.log('error from worker', e )
      }
    } else {


      let target = this.getIntersectionWithEarth(this.cursor.position)
      let targetEdge = this.getIntersectionWithEarth(this.cursor.edge)
      this.getTargetLatLong(target, targetEdge)
    }
    this.lastEventTimestamp = new Date( ).getTime( )
  }

  getIntersectionWithEarth( _mouse, camera = this.camera, object = this.earth.sphere ) {

    let mouse = {
      x: ((_mouse.x - 1) / this.mount.clientWidth) * 2 - 1,
      y: -((_mouse.y - 1) / this.mount.clientHeight) * 2 + 1
    }
    raycaster.setFromCamera(mouse, camera)

    return raycaster.intersectObject( object )
  }

  getTargetLatLong( target, targetEdge ) {

    let center = Geographer.getEventCenter.call(target[0],  radius)
    let edge = Geographer.getEventCenter.call(targetEdge[0], radius)
    if( DEBUG ){
      var c = Geographer.longLatToVect3([center[1],center[0]])//[center[1],center[0]]
      mesh1.position.set(c.x,c.y,c.z);
      var e = Geographer.longLatToVect3([edge[1],edge[0]])
      mesh2.position.set(e.x,e.y,e.z);
    }
    let currentCursorRadius = Math.abs( center[1] - edge[1] )
    if (this.earth.maps["countries"]) {

      let companiesNearBy = this.locations.getNearByCompanies(center, currentCursorRadius)
      let filterMessage = 'No Companies found'
      if ( companiesNearBy.loc.city ) {
        filterMessage = `Companies in ${companiesNearBy.loc.city}, ${companiesNearBy.loc.state?companiesNearBy.loc.state+' ':''}${companiesNearBy.loc.country}`
      }
      else if ( companiesNearBy.loc.country ) {
        filterMessage = `Companies near ${companiesNearBy.loc.state?companiesNearBy.loc.state+', ':''}${companiesNearBy.loc.country}`
      }

      if ( DEBUG ) console.log(`Globe.you landed in `, companiesNearBy.loc.country, companiesNearBy.loc.state, '\n and this comapnies are near by', companiesNearBy.list)
      this.props.updateByIDs(companiesNearBy.list, filterMessage)

    }

  }

  handleWindowResize = ( ) => {

      const width = this.mount.clientWidth
      const height = this.mount.clientHeight

      this.renderer.setSize( width, height )
      this.camera.aspect = width / height
      // Note that after making changes to most of camera properties you have to call
      // .updateProjectionMatrix for the changes to take effect.
      this.camera.updateProjectionMatrix( )
      this.setState({ size: { width, height } })

  }

  zoomUpdate( dir ) {
    if( !window.BrowserDetect.mobile ){
      this.setState({ zoom: 100-round(this.controls.zoomBar( dir ) * 100, 3) })
    }
  }

  render( ) {
    if ( DEBUG ) console.log("Globe. Render")

    if ( !this.props.active && this.props.active !== this.lastActiveState ) this.stopAnimationLoop( )
    else if( this.props.active && this.props.active !== this.lastActiveState) this.startAnimationLoop( )
    this.locations.update(this.props.companies)
    // const loca = ( <Locat locations={this.props.companies} origin={origin} radius={radius} /> )
    return (<div id="globe"  ref={ref => (this.mount = ref)} className={this.props.className}>
      <Cursor size={this.state.size} camera={this.camera} containerID="globe" hoist={this.cursor} />
      {window.BrowserDetect.mobile ? null: <ZoomBar zoomLevel={this.state.zoom} onChange={this.zoomUpdate.bind( this )} />}
    </div>)
  }
}


export default Globe
