import React, { useState } from 'react'
import {useSpring, animated} from 'react-spring'

import './main.scss'

const DEBUG = !true

const Disclosure = ( props ) => {

  let toggle = props.showCopy
  
  const [className, setClassName] = useState(toggle? 'copy':'copy disable' )

  const [propsStyles, setPropsStyles, stop] = useSpring(() => ({opacity: toggle ? 1 : 0}))


  const open = (e) => {
    e.preventDefault()
    e.stopPropagation()
    
    setClassName("copy")
    setPropsStyles({opacity: 1 })
    
  }

  const close = (e) => {
    e.preventDefault()
    e.stopPropagation()
    setClassName("copy disable")
    setPropsStyles({opacity: 0 })
  }

  const ignore = (e) => {
    e.preventDefault()
    e.stopPropagation()
  }

  const disclosure = (
    <animated.div style={propsStyles} className={className} onClick={ignore}>
    <button className="close" onClick={close} ><div className="hit-area"/></button>
    <div className="center">
      <h3> Disclosure </h3>
      {props.copy}
      </div>
    </animated.div>

  )

  if ( DEBUG ) console.log("Disclosure")
  return (
    <div id="disclosure" className={props.className}>
      <button className="open-button" onClick={open}>
        DISCLOSURE
        </button>
      {disclosure}
      
    </div>
  )

}

export default Disclosure

