import React, { useRef } from 'react'

import { useSpring, animated } from 'react-spring'
import VistaColors from '../Colors.js';

import './main.scss'

//import fireCustomGoogleTag from '../gtm-tracking.js';
import { fireCustomGoogleTagEvent } from '../../gtm.js';

// import '../../styles/mixins.scss'

const DEBUG = !true

const List = ( props ) => {
  const selected = ( e ) => {
    e.preventDefault( )
    const currentTargetId = e.currentTarget.attributes['data-id'].nodeValue
    if ( DEBUG ) console.log( `\nList.selected` , currentTargetId )
    props.setSelectedCompany(currentTargetId)

    fireCustomGoogleTagEvent(0, props.nearBy);
  }

  const propsStyle = useSpring({
    from: { opacity: 0, transform: "translateY(40%)" },
    to: { opacity: 1, transform: "translateY(0)" },
    reset: true
  });

  const collapse = ( e ) => {
    e.preventDefault()
    if ( DEBUG ) console.log("List.collapse", ListRef, e)
    ListRef.current.classList.toggle('active')
    ListRef.current.classList.toggle('collapsed')

  }
  const ListRef = useRef( )
  const listMessage = props.message? props.message:"Filtered companies"
  const companiesList = (
    <div id={"list"}>
      <h4 className="message" onClick={collapse}> {props.nearBy.length?props.nearBy.length:''} {listMessage} 
        <button className="collapse-btn" > <span></span> </button>
      </h4>

      <ul>
        {props.nearBy.map((company, i) => {
          var color = { 'color': `${VistaColors.verticals[company.industry_vertical]}` }
          if(company.name === 'Vista') return null
          return (
            <li key={i} style={color}>
              <animated.div style={propsStyle}>
                <button style={props} className={'btn'} onClick={selected} data-id={company.id}>
                  <h6> {company.industry_vertical}</h6>
                  <h4 dangerouslySetInnerHTML={{__html:company.name}} />
                  <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 60 60">
                    <defs><style>{`
                          .circle-${i} {
                            stroke:${VistaColors.verticals[company.industry_vertical]};
                          }
                          .cls-${i} {
                            fill: ${VistaColors.verticals[company.industry_vertical]};
                          }`}
                    </style></defs>
                    <path id="Path_542" className={`cls-${i}`} d="M30.1,9l-3.5,3.5l13.7,13.7H10.4v4.9h29.9L26.6,44.9l3.5,3.5l19.7-19.7L30.1,9z" />
                    <circle className={`circle-${i}`} cx="30.1" cy="28.7" r="27" />
                  </svg>
                </button>
              </animated.div>
            </li>
          )
        })}
      </ul>
    </div>
  )
  
  return (
    <div id={"list-wrapper"} className={`${props.className} ${props.nearBy.length?' active':' collapsed'}`} ref={ListRef}>
      {companiesList}
    </div>
  )
};

export default List