// globeTHREE -> Locations.js
import * as THREE from 'three'
// import { longLatToVect3,
//         wireframe,
//         graticule10 } from './geoHelpers.js';
import VistaColors from '../Colors.js'
// import {longLatToVect3} from './Geographer'

let DEBUG = !true
let cityClusters = { }
let colorTrack = { }
let currentLevel = ''
let loaded = false
export default class Location {

  constructor( { radius, origin = new THREE.Vector3( ) } ) {

    this.radius = radius+1
    this.lastLocations = []
    this.currentLocations = []
    this.origin = origin
    this.group = new THREE.Group()
    this.group.name = "locations"
    this.group.position.set(this.origin.x, this.origin.y, this.origin.z)
    this.group.rotateX(-Math.PI / 2)
    // maybe just retrun locations???
    if ( DEBUG ) console.log( `Location.constructor` )

  }

  update( companies ) {

    if ( DEBUG ) console.log(`Location.update`, companies)

    if ( companies.length && !loaded ) {
      if ( DEBUG ) console.log( `LOADING LOCATIONS FOR THE FIRST TIME!`)
      this.create( companies )
      loaded = true
    }
    else{
      this.showHide( companies )
    }

  }

  showHide ( companies ) {
    // log
    this.group.children.forEach( location =>{
      const found = companies.find( elem => elem.id === location.companyId )
      if (found) location.visible = true
      else location.visible = false
    })
  }

  create( companies ) {

    //iterate through all the list
    companies.map(company => {

      this.group.add(this.createLocation(company, company['hq_location']))

      company['other_locations'].map(loc => {

        this.group.add(this.createLocation(company, loc))
        return null
      })
      return null
    })
    this.setZoomPositions( )
  }

  createLocation( company, place ) {

    if (place.geo_point) {
      place.geo_point.lat = parseInt(place.geo_point.lat)
      place.geo_point.lng = parseInt(place.geo_point.lng)
      if ( DEBUG ) console.log(`Location`, place.city, place.geo_point);
      let hqLocVec3 = this.longLatToVect3(place.geo_point)


      let locationMaterial = new THREE.MeshBasicMaterial({
        color: VistaColors.verticals[company.industry_vertical],
        side: THREE.BackSide,
        wireframe: false,
        transparent: true,
        opacity: 1
      })

      let loc = new THREE.Mesh(locationGeometries.FAR, locationMaterial)
      loc.city = place.city
      loc.state = place.state
      loc.country = place.country
      loc.companyId = company.id
      loc.name = company.name
      loc.group = company.group
      loc.industry_vertical = company.industry_vertical
      loc.positions = {
        CURRENT: place.geo_point,
        ORIGINAL: place.geo_point,
        OTHER: this.longLatToVect3( place.geo_point )
      }
      loc.position.copy(hqLocVec3);
      loc.lookAt(this.origin.x, -this.origin.y / 2, this.origin.z);
      cityClusters[place.geo_point.address] = cityClusters[place.geo_point.address] || [ ]
      cityClusters[place.geo_point.address].push(loc)
      return loc
    }

  }

  setZoomPositions(  ) {
    // On load calculate all the possible Vector3 positions that the locations can have and store them.
    // this should save computation time when interacting
    if ( DEBUG ) console.log( `Locations.setZoomPositions`, this.group.children.length, cityClusters)

    for (var city in cityClusters) {
      if (cityClusters.hasOwnProperty(city)) {
        let cols =''
        let length = cityClusters[city].length

        if(length < 6) cols = 2
        else if (length >=6 && length < 13 ) cols = 3
        else if (length >= 13 ) cols = 4
        let i = 0
        for (var x = 0, xInc = 0; x <= cols; x++, xInc+=0.23) {
          for (var y = 0, yInc = 0; y < Math.ceil(length/cols); y++, yInc+=0.19) {
            // let i = y + x * cols
            if(i < length ){
              cityClusters[city][i].positions.CURRENT.lat += xInc
              cityClusters[city][i].positions.CURRENT.lng -= yInc
              cityClusters[city][i].positions.NEAR = this.longLatToVect3( cityClusters[city][i].positions.CURRENT )
            }
            // try {
            //   // cityClusters[city][i].scale.set(1, 1, 1)
            // } catch (e) { }
            i++
          }
        }
      }
    }
  }

  updateZoom( level ){
    if ( DEBUG ) console.log( `Locations.updateZoom`, level , this.group.children.length, cityClusters)

    switch ( level ) {
      case "NEAR":
        for (var i = 0; i < this.group.children.length; i++) {
          this.group.children[i].geometry = locationGeometries[level]
          this.group.children[i].positions.TARGET = this.group.children[i].positions.NEAR
        }

        break
      default:
        for (var l = 0; l < this.group.children.length; l++) {
          this.group.children[l].geometry = locationGeometries[level]
          this.group.children[l].positions.TARGET = this.group.children[l].positions.OTHER
        }
    }
    animatePos(this.group)

    currentLevel = level
  }

  getNearByCompanies( center, range, locationFilter ) {
    
    let nearbyCompanies = []
    let trak = {}
    let inTheCountry
    let refLoc = {city: null, state: null, country:null}
    let refLocPrev = {city: null, state: null, country:null}
    // if ( DEBUG ) console.log( `\n\tLocation.getNearByCompanies`, distance, range )
    inTheCountry = this.group.children
    // console.log(center, range, locationFilter );
    // HACK: above lat 65 the range starts being insanely high. so just clamp it
    if (center[1] > 64.5) range = 7.5
    for (var i = 0; i < inTheCountry.length; i++) {
      let distance = distanceFromCenter(center, inTheCountry[i].positions.ORIGINAL)
      // let distance = distanceFromCenter( center, inTheCountry[i].geometry.coordinates );
      if (distance < range) {
        // only report unique companies.
        if (trak[inTheCountry[i].companyId] === undefined) {
          // if (inTheCountry[i].companyId !== "Vista") {
            trak[inTheCountry[i].companyId] = inTheCountry[i].companyId
            nearbyCompanies.push(inTheCountry[i].companyId)
            refLoc.city = refLocPrev.city === null || refLocPrev.city === inTheCountry[i].city?inTheCountry[i].city:0
            refLoc.state = inTheCountry[i].state
            refLoc.country = inTheCountry[i].country
            
            refLocPrev = refLoc
            
          // }
        }
      }
    }
    if ( DEBUG ) console.log("\ngetNearByCompanies", refLoc)
    return {list:nearbyCompanies, loc:refLoc }
  }

  longLatToVect3( { lat, lng } ) {
    // convert to radians
    // let theta = lat * Math.PI / 180,
    //     phi = lng * Math.PI / 180,
    //     cosPhi = Math.cos(phi)
    let theta = lng * Math.PI / 180,
        phi = lat * Math.PI / 180,
        cosPhi = Math.cos(phi)

    return new THREE.Vector3(
      this.radius * cosPhi * Math.cos(theta),
      this.radius * cosPhi * Math.sin(theta),
      this.radius * Math.sin(phi)
    )

  }

}

function animatePos( locs ) {

  let clock = new THREE.Clock();
  let duration = 2;
  let lastTime = 0;
  let tick = 0;

  let anim = () => {
    let elapsed = clock.getElapsedTime();
    let delta = elapsed - lastTime; //clock.getDelta( );
    let normalized = elapsed / duration;
    tick += delta;

    // console.log(delta, tick, elapsed);
    if (tick < 0) tick = 0;
    if (delta > 0 && elapsed < duration) {
      // let scale = 2 - Math.pow(normalized, 8);
      for (var i = 0; i < locs.children.length; i++) {
        locs.children[i].position.lerp(locs.children[i].positions.TARGET, normalized);
      }
    } else if (elapsed > duration) {


    }

    lastTime = elapsed;
    if (elapsed < duration * 1.1) {
      // still running
    }
    else{
      clearInterval(posAnim)
    }
  }
 let posAnim = setInterval(anim,1)
  // window.addEventListener("animationFrame", anim);
}

function animateColors() {
  let resetOnce = true;
  // console.log("one country",this );
  let clock = new THREE.Clock();
  let duration = 0.5;
  let lastTime = 0;
  let tick = 0;

  let anim = ( ) => {
    if (currentLevel !== "NEAR") {
      let elapsed = clock.getElapsedTime();
      let delta = elapsed - lastTime; //clock.getDelta( );
      let normalized = elapsed / duration;
      tick += delta;
      if (delta > 0 && elapsed < duration) {
        // let scale = 2 - Math.pow(normalized, 8);

        // cityClusters["Austin United States"][colorTrack["Austin United States"]].material.opacity = 1-normalized;
        for (var city in cityClusters) {
          if (cityClusters.hasOwnProperty(city)) {
            if (cityClusters[city].length > 2) {
              let dir = colorTrack[city].dir === 1 ? 0 : 1;
              // console.log(Math.abs(1 - (dir+normalized)) );
              try {
                cityClusters[city][colorTrack[city].current - 1].material.opacity = Math.abs(dir - normalized);
                // console.log(cityClusters["Austin United States"].length, colorTrack["Austin United States"].current, Math.abs(dir - normalized) );

              } catch (e) {
                console.log(colorTrack[city].current, cityClusters[city].length);
              } finally {

              }
            }

          }
        }
      } else if (elapsed > duration) {


      }

      lastTime = elapsed;
      if (elapsed > duration * 1.01) {
        // times up reset
        for (var city in cityClusters) {
          if (cityClusters.hasOwnProperty(city)) {
            if (cityClusters[city].length > 2) {
              if (1 === colorTrack[city].current) {
                colorTrack[city].dir = 1;
              } else if (cityClusters[city].length === colorTrack[city].current) {
                colorTrack[city].dir = -1;
              }
              colorTrack[city].current += colorTrack[city].dir;
            }
          }
        }
        resetOnce = true;
        clock.stop()
        clock.start();
      }
    } else {
      // reset all opacity;
      if (resetOnce) {
        console.log("Reseting opcaity");
        for (var city in cityClusters) {
          if (cityClusters.hasOwnProperty(city)) {
            for (var i = 0; i < cityClusters[city].length; i++) {
              cityClusters[city][i].material.opacity = 1;
              colorTrack[city].current = cityClusters[city].length;
              colorTrack[city].current = -1;
            }
          }
        }
        resetOnce = false;
      }
    }
  };
  window.addEventListener("animationFrame", anim);
}


function distanceFromCenter(center, {
  lat,
  lng
}) {
  return Math.sqrt(Math.pow(lat - center[0], 2) + Math.pow(lng - center[1], 2));
}



const locationGeometries = {
  NEAR: new THREE.CircleBufferGeometry(0.4, 24),
  MID: new THREE.CircleBufferGeometry(1, 24),
  FAR: new THREE.CircleBufferGeometry(1.2, 32),
  DEFAULT: new THREE.CircleBufferGeometry(1.5, 32),
  DEFAULT_HQ: new THREE.CircleBufferGeometry(2.4, 32),
}


