import React from 'react';
import PropTypes from 'prop-types';
import * as THREE from 'three';

const DEBUG = !true

let svgFillNone = {
  'fill': 'none'
}
let svgStroke = {
  'fill': 'none',
  'stroke' : '#fff',
  'strokeWidth': "5px",
}
let svgStrokeNone = {
  'stroke' : 'none'
}
let svgFill = {
  'stroke' : 'none',
  'fill': "#fff"
}
let svg = {
  'position': 'absolute'

}

let cursorMaterial = new THREE.MeshBasicMaterial( { color: 0xff0000 , side: THREE.DoubleSide, wireframe: false } );

export default class Cursor extends React.Component {
  constructor( props ) {
    super( props )
    if ( DEBUG ) console.log( `Cursor`, props );

    this.origin = props.origin || new THREE.Vector3( )
    this.containerID = props.containerID
    this.diameter = props.diameter || 80

    let originGeo = new THREE.SphereGeometry(10, 20, 10);
    this.originObject = new THREE.Mesh(originGeo);
    this.originObject.position.set(this.origin.x, this.origin.y, this.origin.z);

    let cursorElement = document.getElementById("containerID")
    this.cursorElement = React.createRef( )
    this.state = {
      style:{
        "top":`${this.props.size.height - (this.diameter) }px`,
        "left": `${this.props.size.widht - (this.diameter) }px`,
        "width": `${this.diameter}px`,
        "height": `${this.diameter}px`,
        'position': 'absolute',
      }
    }
    window.addEventListener( 'resize', this.centerOnOrgin.bind(this), false )
  }

  componentDidMount( ){
    setTimeout(this.centerOnOrgin.bind(this),300)
  }

  centerOnOrgin( ) {
    let screenOriginPos = this.toScreenPosition(  )
    if ( screenOriginPos ){

      this.props.hoist.position = {
        x: screenOriginPos.x ,
        y: screenOriginPos.y + (this.diameter/2),
      }
      this.props.hoist.edge = {
        x: screenOriginPos.x + (this.diameter/2),
        y: screenOriginPos.y + (this.diameter/2),
      }

      let style = {
        "top":`${screenOriginPos.y - (this.diameter/2) }px`,
        "left": `${screenOriginPos.x - (this.diameter/2) }px`,
        "width": `${this.diameter}px`,
        "height": `${this.diameter}px`,
        'position': 'absolute',
        'pointerEvents':'none',
      }

      this.setState({style:style})
    }

  }
  toScreenPosition (obj = this.originObject, camera = this.props.camera ) {
    // if ( DEBUG ) console.log( `Cursor.toScreenPosition`, obj, camera);

    if ( camera ) {
      var vector = new THREE.Vector3( )
      // TODO: need to update this when resize window
      var widthHalf = 0.5*this.props.size.width
      var heightHalf = 0.5*this.props.size.height

      obj.updateMatrixWorld()
      vector.setFromMatrixPosition(obj.matrixWorld)
      vector.project(camera)

      vector.x = ( vector.x * widthHalf ) + widthHalf
      vector.y = - ( vector.y * heightHalf ) + heightHalf

      return {
          x: vector.x,
          y: vector.y
      }
    }

  }

  render( ) {
    return (
      <div className="circle" id="cursor" ref={this.cursorElement} style={this.state.style}>
        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 150 150" style={svg}>
          <g id="Group_7902" data-name="Group 7902" transform="translate(-949 -1145)">
            <g id="Path_774" data-name="Path 774" className="cls-1" transform="translate(949 1145)" style={svgFillNone}>
              <path className="cls-4" d="M 75 12 C 40.26168823242188 12 12 40.26168823242188 12 75 C 12 109.7383117675781 40.26168823242188 138 75 138 C 109.7383117675781 138 138 109.7383117675781 138 75 C 138 40.26168823242188 109.7383117675781 12 75 12 M 75 0 C 116.4213409423828 0 150 33.57865905761719 150 75 C 150 116.4213409423828 116.4213409423828 150 75 150 C 33.57865905761719 150 0 116.4213409423828 0 75 C 0 33.57865905761719 33.57865905761719 0 75 0 Z" style={svgFill}/>
            </g>
            <g id="Group_7898" data-name="Group 7898" transform="translate(443 210)" style={svgFillNone}>
              <line id="Line_4" data-name="Line 4" className="cls-2" x1="50" transform="translate(556.5 1010.6)" style={svgStroke}/>
              <line id="Line_1404" data-name="Line 1404" className="cls-2" x1="50" transform="translate(581.5 985.6) rotate(90)" style={svgStroke}/>
            </g>
          </g>
        </svg>
      </div>
    )
  }
}
