import React from 'react'
import './main.scss'
const DEBUG = !true
// [Function Components] (https://reactjs.org/tutorial/tutorial.html#function-components)
const Instructions = ( props ) =>{
  const iconDesktop = (<img alt="back home button" src={`${window.location.protocol}//${window.location.hostname}/data/assets/Instructions_desktop.svg`}/>)
  const iconMobile = (<img alt="back home button" src={`${window.location.protocol}//${window.location.hostname}/data/assets/Instructions_mobile.svg`}/>)
  
  const handleClick = ( e ) => {    
    if ( DEBUG ) console.log( e.currentTarget, e.target )
    if (e.target.nodeName !== "BUTTON"){
      e.preventDefault()
      e.stopPropagation()
    }
  }
  return(
    <div id="instructions-wrapper" className={props.className} onClick={handleClick}>
      <div id="instructions">
        <h4> { props.title }</h4>
        <p>
        { props.copy }
        </p>
        <div className="icons">
        { window.BrowserDetect.mobile ? iconMobile : iconDesktop }
        <button >Let's go</button>
        </div>
      </div>
    </div>
  )
}

export default Instructions