import React, { useState, useRef } from 'react'
import { useSpring, useTransition, animated} from 'react-spring'
import './main.scss'
import Card from './Card'




const DEBUG = !true

const Company = ({ companies, ...props }) =>{
  
  
  const [shouldClose, setShouldClose] = useState(false)
  const [onInitialCard, setOnInitialCard] = useState(true)
  const [index, setIndex] = useState(props.selectedCompanyIndex)
  const [movingDirection, setMovingDirection] = useState(null)
  const [arrowShow, setArrowShow] = useState({prev: companies.length===1?"hide":"show", next: companies.length===1?"hide":"show"})
  const [touch, seTouch] = useState({
    clientX: null,
    timeStamp: null,
  })
  const cardRef = useRef()
  
  let transformFrom = "translate3d(100%, 0 ,0)"
  let transformEnter = "translate3d(0%, 0, 0)"
  let transformLeave = "translate3d(-100%, 0, 0)"

    
  const [cardTransforms, setCardTransforms] = useState({
    from: { opacity: 0 },
    enter: { opacity: 1 },
    leave: { opacity: 0 }
  })
  const [bgSpring, setBgSpring ] = useSpring(()=>({opacity:1}));
  const [cards, setCards] = useState( [ companies[index] ])
  
  // let config = window.BrowserDetect.mobile? { duration: 2000 }: { mass: 1, tension: 210, friction: 30 }
  let config = { duration: 300 }
  // console.log("Card nex ", companies , cards, index )
  const cardTransition = useTransition(cards, item => item.id , {
    config: config ,
    from: cardTransforms.from,
    enter: cardTransforms.enter,
    leave: cardTransforms.leave,    
    reset: true,
    unique: true,
    onDestroyed: ( e ) => {
      if ( DEBUG ) console.log("On onDestroyed! ", e )
    },
    onRest: ( e ) => {
      if( shouldClose ) {
        if ( DEBUG ) console.log( "Company.UNMOUNTING ", e)
        props.setSelectedCompany(null)
      } 
    }
  })

  const nextCompany = ( e ) => {
    
    let shouldSlide = (companies.length > 1)
    setMovingDirection( 'RIGHT' )
    setCardTransforms({
      from: { opacity: 1, transform: transformFrom},
      enter: { opacity: 1, transform: transformEnter},
      leave: { opacity: 1, transform: transformLeave}
    })
    setBgSpring({opacity:1})
    setOnInitialCard( !shouldSlide)
    // this looks weird but basically index is referencing the previous state
    // that is why we setCards with index + 1
    const indx = ( index+1 >= companies.length-1)? 0 : index + 1
    setCards( [companies[indx+1]] )
    setIndex( indx )
    if ( DEBUG ) console.log(" Company.nextCompany",( index === companies.length-1), companies.length ,shouldClose, index, cards)

  }

  const previousCompany = ( ) => {

    let shouldSlide = (companies.length > 1)
    transformFrom = "translate3d(-100%, 0 ,0)"
    transformLeave = "translate3d(100%, 0, 0)"
    setMovingDirection( 'LEFT' )
    setOnInitialCard( !shouldSlide)
    setCardTransforms({
      from:{ opacity: 1, transform: transformFrom},//0
      enter: { opacity: 1, transform: transformEnter},
      leave: { opacity: 1, transform: transformLeave}//0
    })
    setBgSpring({opacity:1})
    // console.log(" Company.previousCompany",( index <= 0 ), companies.length , index, cards)
    const indx = index-1 <= 0 ? companies.length - 1 : index - 1
    setCards( [companies[indx-1]] )
    setIndex( indx )
    if ( DEBUG )  console.log(" Company.previousCompany",( index <= 0 ), companies.length , index, cards)
  }
  const toggleDetail = ( e )=>{
    cardRef.current.classList.toggle('detail-active')
    cardRef.current.classList.toggle(`${e}-active`)
    if ( DEBUG ) console.log("toggleDetail", e)
  }
  const closeCompany = ( )=> {
    // setReset( true )
    setShouldClose( true );
    setCardTransforms({
      from: { opacity: 1 },
      enter: { opacity: 0 },
      leave: { opacity: 0 }
    })
    setBgSpring({opacity:0})
    // make force an update. make it seem like it's differen data 
    setCards( [{...companies[index], id:"same"}] )
    // setCards( [{...companies[index]}] )
  }

  if (onInitialCard){
    if ( DEBUG ) console.log("\n\n\n\n\nCompany", index,shouldClose, onInitialCard)
  }
  else{
    if ( DEBUG ) console.log(">>\nCompany", index,shouldClose, onInitialCard)
  }
  const onTouchStart = (e)=>{
    seTouch({
      clientX:  e.touches[0].clientX,
      timeStamp: e.timeStamp,
    })
  }
  const onTouchEnd = (e)=>{
    //ensure it is not a super slow drag, or tiny tiny swipe
    if(e.timeStamp-touch.timeStamp  < 800 && Math.abs(e.changedTouches[0].clientX-touch.clientX )> 50 ){
      if( e.changedTouches[0].clientX > touch.clientX ){
        // console.log("right", e.timeStamp-touch.timeStamp , Math.abs(e.changedTouches[0].clientX-touch.clientX ))
        previousCompany()
      }else{
        nextCompany()
        // console.log("left", e.timeStamp-touch.timeStamp , Math.abs(e.changedTouches[0].clientX-touch.clientX ) )
      }
    }
  }
  const triggerTrainingMode = ()=>{
    cardRef.current.classList.toggle('training-mode')
    setTimeout(()=>{
      cardRef.current.classList.toggle('training-mode')
    }, 2600)
  }
  
  return (
    <div className="company-wrapper" >
        <animated.div className="background" style={bgSpring} />
          {cardTransition.map( ({ item, props, key }, i)  =>
            <animated.div id="company" style={props} key={key} ref={cardRef} onTouchStart={onTouchStart} onTouchEnd={onTouchEnd}>
              {/* {console.log(props)} */}
              {shouldClose? null: <Card company={item} movingDirection={movingDirection} shouldClose={shouldClose} onInitialCard={onInitialCard} toggleDetail={toggleDetail} triggerTrainingMode={triggerTrainingMode} />}
              <button className="close" onClick={closeCompany}> <p>Back Home</p><img alt="back home button" src={`${window.location.protocol}//${window.location.hostname}/data/assets/back_home@2x.png`}/> </button>
              <button className={`arrow-previous ${arrowShow.prev}`} onClick={previousCompany}><svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 17.89 41.52"><defs><style>{`.cls-1{fill:none;stroke:#231f20;stroke-miterlimit:10;}`}</style></defs><title>prev</title><polyline className="cls-1" points="17.5 0.32 0.65 20.76 17.5 41.2"/></svg>  </button>
              <button className={`arrow-next ${arrowShow.next}`} onClick={nextCompany}>  <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 17.89 41.52"><defs><style>{`.cls-1{fill:none;stroke:#231f20;stroke-miterlimit:10;}`}</style></defs><title>next</title><polyline className="cls-1" points="0.39 0.32 17.24 20.76 0.39 41.2"/></svg> </button>
            </animated.div>
          )}

    </div>
  )

}

export default Company
