import React from 'react'
import './main.scss'

const DEBUG = !true
const ZoomBar = ( props ) => {
    const increment = 1
    let onZoom = ( event ) =>{
      event.preventDefault()
      if (event.currentTarget.className === 'plus') props.onChange( -increment )
      else props.onChange( +increment )
    }
    // let zoomOut = ( event ) =>{
    //   event.preventDefault()
    //
    // }
    const zoomLevel = {"width": `${props.zoomLevel}%`}
    return (
        <div id="zoom-bar" >
          <div  className="bar" style={zoomLevel}/>
            <button className="plus" onClick={onZoom.bind(this)}>+</button>
            <button className="minus" onClick={onZoom.bind(this)}>-</button>
        </div>
    )
}

export default ZoomBar;
