import React, { Component } from "react"
import ReactDOM from "react-dom"
import axios from "axios"
import { Spring, config } from "react-spring/renderprops"
import BrowserDetect from 'browser-detect'

// Components

import Globe from './components/Globe'
import Filter from './components/Filter'
import Company from './components/Company'
import List from './components/List'
import Instructions from './components/Instructions'
import Disclosure from './components/Disclosure'
import Colors from './components/Colors'


// styles

import './styles/style.scss'
// import { bigIntLiteral } from "@babel/types";



const DEBUG = !true

let styleColors = {}

Object.entries(Colors).forEach(level => {
  Object.entries(level[1]).forEach(color => {
    styleColors[`--${level[0]}-${color[0].toLowerCase().replace(/ /g, "-").replace(/,/g, "").replace(/&/g, "n")}`] = color[1]
  })
})

window.BrowserDetect = BrowserDetect()

class App extends Component {

  constructor(props) {

    super(props)
    this.helpTimerDuration = 400000
    this.state = {
      activeGroup: 'All',
      activeVertical: 'All',
      companiesByFilter: [],
      allCompaniesInCircle: [],
      filteredCompaniesInCircle: [],
      filterMessage: "No Companies near by",
      selectedCompany: null,
      showHelpBlock: true,
      fullscreen: false,
    }
    this.globeActive = true

    // Initialize the originalCompanies and industryVerticals arrays
    this.originalCompanies = [];
    this.industryVerticals = ['All']; // Giving this 'All' as a way to display all companies
  }

  componentDidMount() {
    this.fetchCompanies();
    
  }
  fetchCompanies() {
    const URL = `http://${window.location.hostname}/wp-json/globeInteractive/v1/all`
    const devURL = `http://${window.location.hostname}/cashed_campany_data.json`
    // makes call to the wordpress REST api
    axios.get(devURL).then(companies => {
      // keep a copy of the original data so we can always restore it
      this.originalCompanies = companies.data
      // download(JSON.stringify(companies.data), `cashed_campany_data.json`, 'text/plain');
      // Create a list of all of the unique industry verticals
      this.originalCompanies.map((company) => {
        let verticalName = company.industry_vertical;
        if (this.industryVerticals.indexOf(verticalName) === -1) {
          this.industryVerticals.push(verticalName);
        }
      })
      // remove vista
      let vistIndx = null; 
      this.originalCompanies.forEach((el, i)=>{
        if(el.name === "Vista") vistIndx = i 
        
      })
      this.originalCompanies.splice(vistIndx,1)
      // assign it to state
      this.setState({ companiesByFilter: companies.data, selectedCompany: null });
      // this.setState({ companiesByFilter: [companies.data.find( c => c.name === "Vista")], selectedCompany: null }); 
      if (DEBUG) console.log(`App:fetchCompanies`, this.state);
    })

  }
  /**
  *  Filter
  *
  */
  filterByGroup(group) {
    const searchGroup = this.state.filteredCompaniesInCircle.length?this.state.filteredCompaniesInCircle:this.originalCompanies
    const filteredCompanies = [...searchGroup].filter(company => {
      const isInGroup = (company.group === group) || (group === 'All');
      const isInVertical = (company.industry_vertical === this.state.activeVertical) || (this.state.activeVertical === 'All')
      return (isInGroup && isInVertical);
    })
    
    this.setState({ activeGroup: group, companiesByFilter: filteredCompanies ,  filterMessage: null, filteredCompaniesInCircle:filteredCompanies })
  }

  filterByVertical(vertical) {
    const searchGroup = this.state.filteredCompaniesInCircle.length?this.state.filteredCompaniesInCircle:this.originalCompanies
    const filteredCompanies = [...searchGroup].filter(company => {
      const isInGroup = (company.group === this.state.activeGroup) || (this.state.activeGroup === 'All')
      const isInVertical = (company.industry_vertical === vertical) || (vertical === 'All')
      return (isInGroup && isInVertical);
    })
    
    
    this.setState({ activeVertical: vertical, companiesByFilter: filteredCompanies, filterMessage: null, filteredCompaniesInCircle:filteredCompanies })
  }

  filterByIDs( ids , filterMessage) {
    let { allCompaniesInCircle } = this.state;
    if (ids) {
      allCompaniesInCircle = ids;
    }
    // debugger;
    const filteredCircledCompanies = [...this.state.companiesByFilter].filter(company => {
      const found = allCompaniesInCircle.find(id => {
        return company.id == id // comparison operator must be type-converting. ie ==
      })
      return found;
    })

    let haveCompaniesChanged = false;
    
    // compare Last array against Current Array to see if they have changed
    if ( this.state.filteredCompaniesInCircle.length !== filteredCircledCompanies.length
      || this.state.filterMessage !== filterMessage){
      haveCompaniesChanged = true;
    }
    else {
      for (let i = filteredCircledCompanies.length; i--;) {
        if (this.state.filteredCompaniesInCircle[i] !== filteredCircledCompanies[i]) {
          haveCompaniesChanged = true;
          break;
        }
      }
    }

    if (haveCompaniesChanged && ids) {
      this.setState({ filteredCompaniesInCircle: filteredCircledCompanies, allCompaniesInCircle: ids, filterMessage: filterMessage});
    }
    
  }




  /**
  *  SelectedCompany
  *
  */
  setSelectedCompany(id) {
    
    let foundCompany = this.state.filteredCompaniesInCircle.find( company => {
      return company.id == id // comparison operator must be type-converting. ie ==
    })

    this.globeActive = foundCompany ? false : true
    this.setState({ selectedCompany: foundCompany })
    if (DEBUG) console.log(`\nApp.setSelectedCompany`, this.state, "\n", foundCompany)
  }




  /**
   *  Instructions
   *
    */
  resetHelpTimer() {

    const shouldReset = this.state.showHelpBlock
    if ( shouldReset ) {
      this.setState({ showHelpBlock: false, fullscreen: true })
      var resizeEvent = window.document.createEvent('UIEvents'); 
      resizeEvent.initUIEvent('resize', true, false, window, 0); 
      window.dispatchEvent(resizeEvent);
      // clear the last timeout to prevent accumulation a shit load of timeouts
    }

    window.clearTimeout(this.helpTimer)

    this.helpTimer = window.setTimeout(() => {
      // console.log("App.resetHelpTimer resetting. ")
      // if we cross the treshold of inactive time reset the state instruction
      // NOTE we also need to hide list and reset other variables at this point
      this.setState({ showHelpBlock: true, fullscreen: false })
      var resizeEvent = window.document.createEvent('UIEvents');
      resizeEvent.initUIEvent('resize', true, false, window, 0); 
      window.dispatchEvent(resizeEvent);
    }, this.helpTimerDuration)

  }

  getSelectedCompany() {
    if (DEBUG) console.log(`App.getSelectedCompany`, this.state.selectedCompany)

    const currentCompanyIndex = this.state.filteredCompaniesInCircle.indexOf(this.state.selectedCompany);

    if (this.state.selectedCompany) {
      return (
        <Company
          selected={this.state.selectedCompany}
          selectedCompanyIndex={currentCompanyIndex}
          setSelectedCompany={this.setSelectedCompany.bind(this)}
          companies={this.state.filteredCompaniesInCircle} />
      )
    }
    else {
      return null
    }
  }

  render() {
    const { showHelpBlock, companiesByFilter, filteredCompaniesInCircle, filterMessage, fullscreen } = this.state;
    
    // if(window.BrowserDetect.mobile && !showHelpBlock) fullscreen = true
    if ( DEBUG ) console.log("App.render")
    return (
      <section 
        className={`${this.props.className} ${fullscreen?'fullscreen':''}`}
        onClick={this.resetHelpTimer.bind(this)} style={styleColors}>
        <button className='fullscreen-btn' onClick={()=>{this.setState({fullscreen:!fullscreen})} } >
          {fullscreen?
          <img src={`${window.location.protocol}//${window.location.hostname}/data/assets/exit-fullscreen.svg`} alt="exit-fullscreen" />:
          <img src={`${window.location.protocol}//${window.location.hostname}/data/assets/enter-fullscreen.svg`} alt="enter-fullscreen" />
          }
          
        </button>
        <Instructions
          className={`${showHelpBlock ? 'show-component' : 'hide-component'}`}
          title={window.globe_interactive.instructions_title} copy={window.globe_interactive.instructions_copy} />
        <Disclosure
          className={`${showHelpBlock ? 'show-component' : 'hide-component'}`}
          showCopy={false} copy={window.globe_interactive.disclosures} />

        <Filter
          className={`${!showHelpBlock ? 'show-component' : 'hide-component'}`}
          industryVerticals={this.industryVerticals}
          updateByVertical={this.filterByVertical.bind(this)}
          updateByGroup={this.filterByGroup.bind(this)} />
        < Globe
          className={`${!showHelpBlock ? 'show-component' : 'hide-component'}`}
          companies={companiesByFilter}
          updateByIDs={this.filterByIDs.bind(this)} active={this.globeActive} />
        <List
          className={`${!showHelpBlock ? 'show-component' : 'hide-component'}`}
          nearBy={filteredCompaniesInCircle}
          message={filterMessage}
          setSelectedCompany={this.setSelectedCompany.bind(this)} />

        {this.getSelectedCompany()}
      </section>
    )
  }

}

ReactDOM.render(<App id="globe-interactive" className={`globe-interactive ${window.BrowserDetect.mobile?'mobile': 'desktop'}`} />, document.getElementById("app"))
