import React, { useRef, useEffect, useState} from 'react'
import { fireCustomGoogleTagEvent } from '../../gtm.js';
// review how to optimize
// https://reactjs.org/docs/hooks-faq.html#how-to-create-expensive-objects-lazily
const DEBUG = !true

const lineHeight = 1.5

const Cities = ( props ) => {
  return(
    <g className={`location-item-${props.index+1}`} key={props.index} style={{transform:`translate(0px, ${1.8+(lineHeight*props.index)}em)`}}>
      <use href="#myDot" x={`0`}  y="-0.4em" width={`0.2em`} height={`0.2em`}/>
      <text className="item" x={`0.5em`} y={0}  textAnchor="left" >
        <tspan >{props.location.city}</tspan>{props.location.state?',':''} <tspan >{props.location.state}</tspan>
      </text>
    </g>
  )
}

const Countries = ( props ) => {
  let padY = 0
  for (let ind = 0; ind <= props.index; ind++) {
    padY += props.region[ind-1]? lineHeight*props.region[ind-1].length : 1.5
    padY += 2 
  }

  return (
    <g className={`location-country-${props.index+1}`} key={props.index} style={{transform:`translate(0px, ${padY}em)`}}>
      <line className="line-country" x1="0" y1="0.2em" x2="100%" y2="0.2em" />
      <text className="item" textAnchor="left" x={0} y={`0em`}>
        <tspan >{props.country[0].country}:</tspan>
      </text>
      {props.children}
    </g>
  )
}


const ScrollBtn = ( {fontSize, width, dir, display, y,...props} ) => {
  const ChevSize = fontSize*0.5
  const pathUP = `M 0 ${ChevSize*.5} L ${ChevSize/4} 0 L ${ChevSize/2} ${ChevSize*.5}`
  const pathDown = `M 0 0 L ${ChevSize/4} ${ChevSize*.5} L ${ChevSize/2} 0`
  
  const onClick = ( e ) =>{
    props.onClick()
    if (DEBUG) console.log(`ScrollBtn`)
  }
  return(
  <g className="scroll-btn" style={{transform: `translate(0,${y-(fontSize)}px)`, display:display?'block':'none'}} >
    <rect className="scroll-btn-bg" x="0" y="0" width={width} height={fontSize*1.2} fill='url(#Gradient2)'onClick={onClick}/>
    <path d={dir?pathUP:pathDown} style={{transform : ` translate(${0}px,${ChevSize/2}px) `}}/>
    <path d={dir?pathUP:pathDown} style={{transform : ` translate(${0}px,${ChevSize/2+(fontSize*.5)*.6}px)`}}/>
  </g>
  )
}

const Regions = ( props ) => {
  
  const lineHeight = 1.7
  const firstLineOffset = 1.55
  const gutter = props.gutter
  
  const regionRef = useRef( )
  
  
  const [param, setParam] = useState(( )=> ({
    scrollable: false,
    fontSize: 24,
    height: props.maxHeight
  }))
  const [scroll, setScroll] = useState(( )=>({
    top:0,
    dir:0
  }))
  
  const [scrollStart , setScrollStart ] = useState(( )=>0)
  const [lastScrollPos, setLastScrollPos] = useState(( )=>0)
  
  
  const onTouchStart = (e)=>{
    if( DEBUG ) console.log(`\n\n\n\tRegions.onTouchStart` )
    setScrollStart(e.touches[0].clientY)
    setLastScrollPos(scroll.top)
  }
  const onTouchMove = ( e ) =>{
    if( DEBUG ) console.log(`\tRegions.onTouchMove` )
    keepInBounds(e.touches[0].clientY-scrollStart)
  }
  const onWheel = ( e ) =>{
    e.preventDefault( )
    e.stopPropagation( )
  
    if( DEBUG ) console.log(`\tRegions.onWheel`, e.deltaY )
    keepInBounds(e.deltaY>0? -4:4 )
    setLastScrollPos(scroll.top)
    return false
  }
  const onScrollBtn = ( e ) =>{
    //set the direction, and increment the height or a large number enough to always get back to the top 
    let delta = (param.fontSize*props.btnSize*lineHeight*firstLineOffset)
    delta = scroll.dir? 10000:-delta
    if( DEBUG ) console.log(`\tRegions.onScrollBtn`, delta )
    //just move a percentage of the height at the time
    setLastScrollPos(scroll.top)
    keepInBounds( delta )
  }
  const keepInBounds = ( delta ) =>{
    const{ height } = regionRef.current.children['column'].getBBox( )
    const countryBlockOffset = (param.fontSize*props.btnSize*lineHeight*firstLineOffset)
    const parentHeight = props.maxHeight-countryBlockOffset
    if( DEBUG ) console.log(`\tRegions.keepInBounds`, lastScrollPos, delta , lastScrollPos+delta, "\t",height-parentHeight, height, parentHeight  )
    if( lastScrollPos+delta <= 0 ){
      if(Math.abs(lastScrollPos+delta) < height - parentHeight  ){
        setScroll({
          dir:  Math.abs(lastScrollPos+delta)> (height - parentHeight )*.9 ? 1:0,
          top: lastScrollPos+delta,
        })
      }
      else{
        if ( DEBUG ) console.log("max")
        setScroll({
          dir: 1,
          top: -1*(height-parentHeight),
        })
      }
    }
    else{
      if ( DEBUG ) console.log("min")
      setScroll({
        dir: 0,
        top: 0,
      })
    }
  }
 
 
  // console.log("Region", actualPadding )

  
  useEffect((  ) => {
    //mounting 
    const {  height } = regionRef.current.children['column'].getBBox( )
    setParam({
      scrollable: height> props.maxHeight?true:false,
    fontSize: parseInt(getComputedStyle(regionRef.current).fontSize),
    height:height
    })
    if ( DEBUG ) console.log("\tRegion.useEffect"  )
    return ( ) => {
      //unmounting
    }
  },[ props.maxHeight ])
  
  return(
  <svg className={`location-region-${props.index+1}`} height={props.maxHeight} width={props.colW*gutter} 
       onTouchMove={param.scrollable?onTouchMove:null} onTouchStart={param.scrollable?onTouchStart:null} onWheel={param.scrollable?onWheel:null} 
       y={`7em`} ref={regionRef} x={ (props.colW*props.index) }>
    
    <g id="column" className="scrollable-column" style={{transform: `translate(0, ${scroll.top}px)`}} >
      <rect className="scroll-bg" width="100%" height={param.height} x="0" y={`${lineHeight*firstLineOffset}em`}/>
      {props.children}
    </g>

    <rect className="scroll-bg" width={props.colW*gutter} height={`${lineHeight}em`} x="0" y="0" />
    <text className="title" textAnchor="left" x={0} y={`1em`}>
      <tspan > {props.region[0][0].region}</tspan>
    </text>
    <line className="line-region" x1="0" y1={`${lineHeight}em`} x2="100%" y2={`${lineHeight}em`} />
    <ScrollBtn fontSize={param.fontSize*props.btnSize} width={props.colW*gutter} dir={scroll.dir} 
          onClick={onScrollBtn} display={param.scrollable} y={props.maxHeight}/>
    
  </svg>
  )
}


const OtherLocations = ( props ) => {
  const thisNodeRef = useRef( )
    
  let regions = []
  const colMinWidht = 100
  const colMaxWidht = 220
  const maxWidthGroup = 1100
  let windowPadding = window.innerWidth*.1
  const btnSize=2
  const gutter = 0.9
  
  const [scrollX , setScrollX ] = useState(( )=>0)
  const [scrollStart , setScrollStart ] = useState(( )=>0)
  let [lastScrollPos, setLastScrollPos] = useState(( )=>0)
  const [param, setParam] = useState(( )=>({
    colWidth: 0,
    maxHeight: 200,
    offsetX: 0,
    groupPadding: 0,
    fontSize: 20,
    actualPadding: 20,
  }) )
  
  const toggleState = ( event ) => {
    
    if ( event.target.parentElement.classList.contains('rc') ){
      // toogle class and reorganize svg staking order
      let thisElement = event.currentTarget.parentElement
      let parentElemtn = event.currentTarget.parentElement.parentElement
      parentElemtn.insertBefore(thisElement,thisElement.children[thisElement.children.length-1].nextSibling)
      thisElement.children['other_locations'].children['shadow'].classList.toggle('shadow')
      // thisElement.children['other_locations'].children['clickable'].classList.toggle('clickable')
      thisElement.children['other_locations'].classList.toggle('active')
      props.toggleDetail("OtherLocations")
      getparams( )
      if(thisElement.children['other_locations'].classList.contains('active')) fireCustomGoogleTagEvent(3, 'Other locations');
      setTimeout( ( ) => {
        // wait 10ms so for the reorganizing the staking order takes effect and turn on 
        thisElement.children['other_locations'].children['location-list'].classList.toggle('detail-show')
        thisElement.children['other_locations'].children['location-list'].classList.toggle('detail-out')
      },10)
    }
  }
  const getparams = ( ) => {
    if(thisNodeRef.current === undefined) return
    const {x,y, width} = thisNodeRef.current.children['clickable'].getBoundingClientRect( )
    const fontSize = parseInt(getComputedStyle(thisNodeRef.current).fontSize)  
    const workingWidth = window.innerWidth-windowPadding>maxWidthGroup?maxWidthGroup:window.innerWidth-windowPadding
    const _colW = workingWidth/regions.length>colMaxWidht?colMaxWidht:workingWidth/regions.length
    const colWidth = _colW<colMinWidht? colMinWidht: _colW
    // const maxHeight = 400+(fontSize*btnSize)
    const maxHeight = (window.innerHeight-(y+props.radius))*.7
    const offsetX = x + (width/2) 
    let actualPadding = (window.innerWidth-((param.colWidth*regions.length)-(param.colWidth*(1-gutter))))/2
      actualPadding = actualPadding < 20 ? 20 : actualPadding
    // setOffsetX( x + windowPadding/2)
    
    setParam({
      colWidth,
      maxHeight,
      offsetX,
      fontSize,
      actualPadding,
    })
    if ( DEBUG ) console.log("getparams\n", "offsetX",offsetX , "window Width:",window.innerWidth, "workingWidth:",workingWidth, "padding", 
      windowPadding,"colwidth", colWidth , "maxHeight",maxHeight, "\n",
      thisNodeRef.current.children['clickable'].getBoundingClientRect( ))//
  }

  useEffect(( )=>{
    getparams( )    
  },[])

  
  const onMouseDown = (e)=>{
    e.preventDefault()
    e.stopPropagation()
    if( DEBUG ) console.log(`OtherLocations.onMouseDown`, e )
    setScrollStart(e.clientX)
    setLastScrollPos(scrollX)
  }
  const onMouseMove = (e)=>{
    e.preventDefault()
    e.stopPropagation()
    if(e.buttons ){
      keepInBounds(e.clientX-scrollStart)
    }
  }
  const onTouchStart = (e)=>{
    e.preventDefault()
    e.stopPropagation()
    if( DEBUG ) console.log(`OtherLocations.onTouchStart`, e.touches[0].clientX )
    setScrollStart(e.touches[0].clientX)
    setLastScrollPos(scrollX)
  }
  const onTouchMove = (e)=>{
    e.preventDefault()
    e.stopPropagation()
    // if( DEBUG ) console.log(`OtherLocations.onTouchMove`, scrollStart, e.touches[0].clientX-scrollStart )
    keepInBounds(e.touches[0].clientX-scrollStart)
  }
  const keepInBounds = ( delta ) =>{
    const {width} = thisNodeRef.current.children['location-list'].children['lateral-scroll-window'].getBoundingClientRect()
    // const scrollGroup = scrollWindow.children['lateral-scroll-grop']
    const scrollWindowWidth = window.innerWidth-(param.actualPadding*2)
    // if( DEBUG ) console.log(`OtherLocations.keepInBounds`, lastScrollPos, delta, Math.abs(lastScrollPos+delta),"\t",width, scrollWindowWidth )
    if( lastScrollPos+delta < 4 ){
      if(Math.abs(lastScrollPos+delta) < width - scrollWindowWidth  ){
        setScrollX( lastScrollPos+delta  )
      }
    }

  }

  if(props.list.length){
    regions = sortRegions( props.list )
    return (
      <svg x={props.pos.x} y={props.pos.y} id="other_locations" className="other_locations rc" onClick={toggleState.bind(this)} ref={thisNodeRef}>
        <symbol id="myDot" viewBox="0 0 2 2">
          <circle cx="1" cy="1" r="1" />
        </symbol>
        <defs><linearGradient id="Gradient2" x1="0" x2="0" y1="0" y2="1">
                <stop offset="0%" stopColor="red"/>
                <stop offset="10%" stopColor="black" stopOpacity="0"/>
            </linearGradient></defs>
        <g id="location-list" className="location-list detail-out" style={{transform:`translate(${-param.offsetX}px, 0)`}}>
          <rect className="bg" x={`-100%`} y={`-100%`} width="400%" height="400%"/>
          {/* <rect className="debug rect" x={((window.innerWidth-((param.colWidth*regions.length)-(param.colWidth*(1-.9))))/2)} y={`0`} width={(param.colWidth*regions.length)-param.colWidth*.1} height="100%" fill="red"/> */}
          <svg x={param.actualPadding} y='0' id='lateral-scroll-window' className="lateral-scroll-window" width={window.innerWidth-(param.actualPadding*2)} height="100%" style={{overflow: 'scroll'}} 
            onMouseDown={onMouseDown} onMouseMove={onMouseMove} 
            onTouchStart={onTouchStart} onTouchMove={onTouchMove}
            >
            <rect className="bg" x='0' y='0' width="100%" height="100%"  />
            <g id='lateral-scroll-grop' style={{transform: `translate(${scrollX}px, 0)`}}> 
            {regions.map( ( region, ri ) =>(
              <Regions key={ri} region={region} index={ri} maxHeight={param.maxHeight} btnSize={btnSize}
                colW={param.colWidth}   total={regions.length} actualPadding={param.actualPadding} gutter={gutter} >
                {region.map( ( country, ci ) =>(
                  <Countries country={country} region={region} index={ci} key={ci}>
                    {country.map( ( location, li ) =>(
                    <Cities location={location} key={li} index={li} />
                    ))}
                  </Countries>
                ))}
              </Regions>
            ))}
            </g>
          </svg>
          </g>
        <circle id="shadow" className={`node shadow rc`} cx="0" cy="0" r={`${props.radius}em`}  />
        <circle id="clickable" className={`node clickable rc`} cx="0" cy="0" r={`${props.radius}em`}  />
        <text className="title large rc" x="0" dy="0" textAnchor="middle" >{props.list.length}</text>
        <text className="sub-title rc" x="0" y="0" textAnchor="middle" >
          <tspan x="0" y="0" dy="1.8em">Other</tspan>
          <tspan x="0" y="0" dy="2.8em">Locations</tspan>
        </text>
      </svg>
    )
  }
  else{
    return null
  }
}

const sortRegions = ( list ) =>{
  let regionNames = []
    // collect region names
    list.forEach( el => {
      if(!regionNames.find( e =>el.region === e)) regionNames.push(el.region)
    })
    // create colums of elements by those regions
    let regionsPresort = regionNames.map( (region, i) => list.filter( l => l.region === region ) )
    // organize data into 3 level array
    return regionsPresort.map( (colum, i) =>{  
      // collect country names in colum
      const o = [] 
      colum.forEach(row => {
        if(!o.find( e =>row.country === e)) o.push(row.country)
      })
      let g = []
      // create groups based on those contries
      o.forEach( group =>{
        g.push(colum.filter( l => l.country === group ))
      })
      return g
    })
}

export default OtherLocations