import * as d3 from 'd3'

import * as d3Geo from 'd3-geo'
import * as THREE from 'three'

let DEBUG =  !true
let radius

export const getPoint = ( { object, face } )=>{
  if ( DEBUG ) console.log( `Geographer.getpoint`,  object  )
  // Get the vertices
  let a = object.geometry.vertices[face.a]
  let b = object.geometry.vertices[face.b]
  let c = object.geometry.vertices[face.c]

  // Averge them together
  let point = {
    x: (a.x + b.x + c.x) / 3,
    y: (a.y + b.y + c.y) / 3,
    z: (a.z + b.z + c.z) / 3
  }

  return point
}

// Converts a point [longitude, latitude] in degrees to a THREE.Vector3.
export function longLatToVect3(point, _r) {
  var r = _r || 0
  var lambda = point[0] * Math.PI / 180,
      phi = point[1] * Math.PI / 180,
      cosPhi = Math.cos(phi)
  return new THREE.Vector3(
    radius * cosPhi * Math.cos(lambda),
    radius * cosPhi * Math.sin(lambda),
    radius * Math.sin(phi)
  )
}
// Converts a GeoJSON MultiLineString in spherical coordinates to a THREE.LineSegments.
export function wireframe(multilinestring, material, _radius) {
  var geometry = new THREE.Geometry()
  radius = _radius
  multilinestring.coordinates.forEach(function(line) {
    d3.pairs(line.map(longLatToVect3), function(a, b) {
      geometry.vertices.push(a, b)
      // geometry.faces.push( new THREE.Face3( 0, 1, 2 ) )
    })
  })

  return new THREE.LineSegments(geometry, material)
}


export function getEventCenter(radius) {

  if(radius === undefined) throw "getEventCenter::please define a radius"
  else if ( this == undefined ) return
  radius = radius || 200
  var point = getPoint( this )
  if ( DEBUG ) console.log( `Geographer.getEventCenter`, point )
  // var point = this.point
  var latRads = Math.acos(point.y / radius)
  var lngRads = Math.atan2(point.z, point.x)
  var lat = (Math.PI / 2 - latRads) * (180 / Math.PI)
  var lng = (Math.PI - lngRads) * (180 / Math.PI)

  return [lat, lng - 180]

}

export function convertToXYZ(point, radius) {
  if(radius === undefined) throw "getEventCenter::please define a radius"
  radius = radius || 200

  var latRads = ( 90 - point[0]) * Math.PI / 180
  var lngRads = (180 - point[1]) * Math.PI / 180

  var x = radius * Math.sin(latRads) * Math.cos(lngRads)
  var y = radius * Math.cos(latRads)
  var z = radius * Math.sin(latRads) * Math.sin(lngRads)

  return {x: x, y: y, z: z}
}

export var geodecoder = function (features) {

  let store = {}

  for (let i = 0; i < features.length; i++) {
    if(features[i].id !== undefined){
      store[features[i].id] = features[i]
    }
    if(features[i].properties !== undefined){
      if(features[i].properties.name !== undefined){
        store[features[i].properties.name] = features[i]
      }
    }
  }

  return {
    find: function (id) {
      return store[id]
    },
    search: function (lat, lng) {

      let match = false

      let country, coords

      for (let i = 0; i < features.length; i++) {
        country = features[i]
        if(country.geometry.type === 'Polygon') {
          match = pointInPolygon(country.geometry.coordinates[0], [lng, lat])
          if (match) {
            return {
              code: features[i].id,
              name: features[i].properties.name
            };
          }
        } else if (country.geometry.type === 'MultiPolygon') {
          coords = country.geometry.coordinates
          for (let j = 0; j < coords.length; j++) {
            match = pointInPolygon(coords[j][0], [lng, lat])
            if (match) {
              return {
                code: features[i].id,
                name: features[i].properties.name
              };
            }
          }
        }
      }

      return null
    }
  };
};

// http://www.ecse.rpi.edu/Homepages/wrf/Research/Short_Notes/pnpoly.html
var pointInPolygon = function(poly, point) {

  let x = point[0];
  let y = point[1];

  let inside = false, xi, xj, yi, yj, xk;

  for (let i = 0, j = poly.length - 1; i < poly.length; j = i++) {
    xi = poly[i][0];
    yi = poly[i][1];
    xj = poly[j][0];
    yj = poly[j][1];

    xk = ((yi > y) !== (yj > y)) && (x < (xj - xi) * (y - yi) / (yj - yi) + xi);
    if (xk) {
       inside = !inside;
    }
  }

  return inside;
};
