import React, { useState, useRef } from 'react'
import Picker from './Picker'
import './main.scss'
// import { all } from 'q';
const DEBUG = !true

const Filter = ( { companies, ...props } ) =>{
    
  const [valueGroups, setValueGroups] = useState({ group: 'All', vertical: 'All' })
  const [optionGroups, setOptionGroups] = useState({ group: ['All', 'Former', 'Current'], vertical: props.industryVerticals })

  const updateGroup = (e) =>{
    const newVal = e.target.value
    console.log('Active Group: ', newVal);
    props.updateByGroup(newVal)
  }

  const updateVertical = (e) =>{
    const newVal = e.target.value
    console.log('Active Industry Vertical: ', newVal);
    props.updateByVertical(newVal)
  }

  // Update the value in response to user picking event
  const handleChange = (name, value) =>{
      setValueGroups({
        ...valueGroups,
        [name]: value
      })
    if (name === 'vertical')
      props.updateByVertical(value)
    else if (name === 'group')
      props.updateByGroup(value)
  }
    
    let verticalsDropdown = (
      <select className="vertical" onChange={updateVertical}>
        {props.industryVerticals.map((vertical, i) => {
          return (
            <option key={i} value={vertical} onChange={updateVertical}>{vertical}</option>
          )
        })}
      </select>
    )

    let groupDropdown = (
      <select className="group" onChange={updateGroup}>
        <option key="0" value="All">All</option>
        <option key="1" value="Former">Former</option>
        <option key="2" value="Current">Current</option>
      </select>
    )
    
    const expand = ( e ) =>{
      let thisElement = e.currentTarget.parentElement.children['filter-expand']
      let picker = e.currentTarget.parentElement.children[1]
      thisElement.classList.toggle("active")
      // if(thisElement.classList.contains("active")){
      //   filter.current.innerHTML = `<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 38 42" ><g><g><polygon  points="19 0 0 0 0 4 13 21 19 21 25 21 38 4 38 0 19 0"/><polygon  points="19 23 13 23 13 36 25 42 25 23 19 23"/></g></g></svg> Filter`
      // }
      // else{
      //   filter.current.innerHTML =  `<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 38 42" ><g><g><polygon  points="19 0 0 0 0 4 13 21 19 21 25 21 38 4 38 0 19 0"/><polygon  points="19 23 13 23 13 36 25 42 25 23 19 23"/></g></g></svg> Filter`
      // }
      picker.classList.toggle("mobile-open")
      
    }
    
    const filter = useRef()
    if ( DEBUG ) console.log("Filter")
    return (
      <div id="filter" className={props.className}>
        <div id='filter-expand' className="expand-btn" onClick={expand} ref={filter}> 
        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 38 42" ><g><g><polygon  points="19 0 0 0 0 4 13 21 19 21 25 21 38 4 38 0 19 0"/><polygon  points="19 23 13 23 13 36 25 42 25 23 19 23"/></g></g></svg>
        Filter  
        </div>
        <Picker
          optionGroups={optionGroups}
          valueGroups={valueGroups}
          onChange={handleChange}
          height={150}
          itemHeight={20} />

      </div>

    )
}

export default Filter 