import React, { useRef, useEffect, useState} from 'react'
const DEBUG = false
const Hq_location = ( props ) => {
    const maxCharacters = 12
    const doubleLine = {
      transform: `translateY(-0.5em)`
    }
    const _country = props.loc.country === "United States"? "USA": props.loc.country
    const l = `${props.loc.city}, ${props.loc.state?props.loc.state+',':''} ${_country}`

    const LocationLabel = ( {city, country, state} )=>{
      
      if (l.length < maxCharacters) {
        return (
          <g>
          <text className="title" x="0" y="0"  textAnchor="middle" >{l}</text>
          <text className="sub-title" x="0" y="0" dy="1em" textAnchor="middle" >headquarters</text>
          </g>
        )
      }
      else{
        return(
          <g>
          <text className="title" x="0" y="0"  style={doubleLine} textAnchor="middle" >
            <tspan x="0" y="0" dy="0">{city},</tspan>
            <tspan x="0" y="0" dy="1em">{state?state+', ':''}{_country}</tspan>
          </text>
          <text className="sub-title" x="0" y="0" dy="2.2em" textAnchor="middle" >headquarters</text>
          </g>
        )
      }
  }
    let rad = props.loc.city.length>maxCharacters-3? maxCharacters/2 : props.radius
     rad = _country.length>maxCharacters-3? maxCharacters/2 : rad
    return (
      <svg x={props.pos.x} y={props.pos.y} className=" hq_location"  onClick={props.triggerTrainingMode}>
        <circle className="node" cx="0" cy="0" r={`${rad}em`}  />
        <LocationLabel city={props.loc.city} country={props.loc.country} state={props.loc.state}/>

      </svg>
    )
} 







const Date_aquired = ( props ) => {
  
  return (
    <svg x={props.pos.x} y={props.pos.y} className=" date_aquired" onClick={props.triggerTrainingMode}>
      <circle className="node" cx="0" cy="0" r={`${props.radius}em`}  />
      <text className="title" x="0" y="0.25em" textAnchor="middle"  >{props.year.split('/')[2]}</text>
    </svg>
  )
}






const Date_exited = ( props ) => {
  
  if(props.year){
    
    return (
      <svg x={props.pos.x} y={props.pos.y} className=" date_exited"  onClick={props.triggerTrainingMode}>
      <circle className="node " cx="0" cy="0" r={`${props.radius}em`}  />
      <text className="title" x="0" y="0.25em" textAnchor="middle" >{props.year.split('/')[2]}</text>
      </svg>
    )
  }
  else{
    return null
  }
}






const Logo = ( props ) => {
  
  const imgRef = useRef()
  const [offsetX, setOffsetX] = useState(0)
  // const [imgWidth, setImgWidth] = useState(0)
  useEffect(()=>{
    setOffsetX(imgRef.current.getBoundingClientRect().width/2)
    // setImgWidth()
  },[])
  
  return (
    <svg x={props.pos.x} y={props.pos.y} className=" logo"  onClick={props.triggerTrainingMode}>
      <mask id="myMask">
        <rect x={`${-props.radius*2}em`} y={`${-props.radius*1.08}em`} width={`${props.radius*4}em`} height={`${props.radius*1.1}em`} fill="white" />
      </mask>
      <circle className="node " cx="0" cy="1" r={`${props.radius}em`} mask="url(#myMask)"  />
      <image x={-offsetX} y="0.6em" href={props.src} height={`${props.radius}em`} width={`${props.radius*3}em`}ref={imgRef} />
    </svg>
  )
}










const Years_of_investment = ( props ) => {
  
  // need to be updated to be more precise
  const exit = props.end?new Date(props.end.split('/')[2],props.end.split('/')[0]-1, props.end.split('/')[1] ):new Date()
  const aquired = new Date(props.begining.split('/')[2],props.begining.split('/')[0]-1, props.begining.split('/')[1] )

  var diff = Math.floor(exit.getTime() - aquired.getTime());
  var day = 1000 * 60 * 60 * 24;

  var days = Math.floor(diff/day);
  var months = Math.floor(days/31);
  // var years = Math.floor(months/12);
  var years = Math.floor(days/365);
  let timeLable = ''
  let timeNumber = ''
  if( years > 0){
    timeLable = "Years"
    timeNumber = years
  }
  else if (years ===0) {
    timeLable = "Months"
    timeNumber = months
  }

  if ( DEBUG )  console.log("Years_of_investment",timeLable, years, months, diff, 
  "\n", aquired,  "\n",exit, "\n",props, 
  "\n",props.begining.split('/')[2], props.begining.split('/')[1]-1, props.begining.split('/')[0] )
  return (
    <svg x={props.pos.x} y={props.pos.y} className=" years_of_investment"  onClick={props.triggerTrainingMode}>
      <circle className="node" cx="0" cy="0" r={`${props.radius}em`}  />
      <text className="title large" x="0" y="0" dy="0em" textAnchor="middle" >{timeNumber}+</text>
      <text className="sub-title" x="0" y="0" dy="0em" textAnchor="middle" >
        <tspan x="0" y="0" dy="1.8em">{timeLable} of</tspan>
        <tspan x="0" y="0" dy="2.8em">investment</tspan>
      </text>

    </svg>
  )
}



export{
  Hq_location,
  Date_aquired,
  Date_exited,
  Logo,
  Years_of_investment,
};
