import React, { useState} from 'react'
import { fireCustomGoogleTagEvent } from '../../gtm.js';
const DEBUG = false

const AddOnCompanies = ( props ) => {
  let [fontSize, setFontSize] = useState(()=>20)
  let [offset, setOffset] = useState(()=>({x: 1, y: 0}))
  
  
  const toggleState = ( event )=>{
    let thisElement = event.currentTarget.parentElement
    let parentElemtn = event.currentTarget.parentElement.parentElement
    if ( DEBUG )  console.info( "AddOnCompanies", thisElement, parentElemtn)
    let thisNode=parentElemtn.insertBefore(thisElement,thisElement.children[thisElement.children.length-1].nextSibling)
    thisElement.children['addOnCompanies'].children['shadow'].classList.toggle('shadow')
    const list = thisElement.children['addOnCompanies'].children['companies-list']
    const {height, width, x, y} =list.children[list.children.length-1].getBoundingClientRect()
    if ( DEBUG )   console.log(`AddOnCompanies`,list.children[list.children.length-1].getBoundingClientRect())

    thisElement.children['addOnCompanies'].classList.toggle('active')
    // thisElement.children['clickable'].classList.toggle('clickable')
    if(thisElement.children['addOnCompanies'].classList.contains('active')){
      fireCustomGoogleTagEvent(2, 'AddonComapnies');
    }
    if(props.list.find(company => company.name.length > 40 )){
      props.toggleDetail("AddOnCompanies")
    }
    setFontSize(parseInt(getComputedStyle(thisNode).fontSize))

    setOffset({
        x:window.innerWidth < 450 ? 0 :1, 
        y:window.innerHeight < 680 ? 0 :1, })

        if ( DEBUG )  console.log(`AddOnCompanies`,"offset", offset, window.innerHeight - (y + height), 'height',height, "window.innerHeight",window.innerHeight )
    setTimeout(()=>{
      const animIn = thisNode.children['addOnCompanies'].children['companies-list'].classList.toggle("detail-show")
      thisNode.children['addOnCompanies'].children['companies-list'].classList.toggle("detail-out")

      
      if ( DEBUG )  console.info( "AddOnCompanies", animIn)
    },10)
    
  }
  const transition = (  )=>{
    const delayInterval = 80
    let t = `
    #company .companies-list.detail-out, #company .companies-list.detail-out rect{
      transition-delay: 0ms;
      transition-delay: ${300+(props.list.length*delayInterval)}ms;
    }
    `
    for (var i = 1; i <= props.list.length; i++) {
      t += `
        .company-item-${i} text, .company-item-${i} line{
          transition-delay: ${((props.list.length-1)*delayInterval) - ((i-1)*delayInterval)}ms !important;
        }
        .company-item-${i} circle{
          transition-delay: ${300+((props.list.length-1)*delayInterval)- ((i-1)*delayInterval)}ms !important;
        }
        .detail-show .company-item-${i} text, .detail-show .company-item-${i} line{
          transition-delay: ${(300+delayInterval) +((i-1)*delayInterval)}ms !important;
        }
        .detail-show .company-item-${i} circle{
          transition-delay: ${i*delayInterval}ms !important;
        }
      `
    }
    return t
  }
  
 
  if(props.list.length){
    return (
      <svg x={props.pos.x} y={props.pos.y} id="addOnCompanies" className="addOnCompanies" onClick={toggleState.bind(this)} >
          
      <style>
        {transition()}
      </style>
      
        <g id="companies-list" className="companies-list detail-out"  >
          <rect className="bg" x={`-100%`} y={`-100%`} width="300%" height="300%" />
          {props.list.map( ( company, i ) =>{
              const radiusOffset = ( props.radius*fontSize )
            return (
              <g className={`circle-item company-item-${i+1}`} key={i} >
                <circle className="node-empty" cx="0" cy="0" r={radiusOffset+((i+1)*fontSize)}  />
              </g>
            )
          })}
          {props.list.map( ( company, i ) =>{
              const radiusOffset = ( props.radius*fontSize )
              const titleOffsetY = offset.x ?  radiusOffset + ( fontSize*props.list.length ) : radiusOffset + ( fontSize*(props.list.length*0.2) )
              const titleOffsetX = radiusOffset - ( fontSize*props.list.length/2 )
              const titleY = titleOffsetY + ( i*(fontSize*2) ) 
              const titleX =  offset.x ?  titleOffsetX+( fontSize*props.list.length ) : fontSize*2
            return (
              <g className={`company-item-${i+1}`} key={i} >
                <line x1="0" y1={(fontSize/2)+radiusOffset+(i*fontSize)}  x2={titleX  - (fontSize/2) } y2={titleY - (fontSize/2)}/>
                <rect className="bg" x={titleX} y={titleY-fontSize} width={`${(company.name.length+company.acquisition_date.length)*0.5}em`} height="1.5em" />
                <text className="title" x={titleX} y={titleY}  textAnchor="left" >
                  <tspan >{company.name}</tspan>
                  <tspan className="date"> {company.acquisition_date}</tspan>
                </text>
                {/* <circle className="node-empty" cx="0" cy="0" r={radiusOffset+((i+1)*fontSize)}  /> */}
              </g>
            )
          })}

        </g>
        <circle id="shadow" className={`node shadow`} cx="0" cy="0" r={`${props.radius}em`}  style={{transform: `translate(0,${0}px)`}}/>
        <circle id="clickable" className={`node clickable`} cx="0" cy="0" r={`${props.radius}em`}  style={{transform: `translate(0,${0}px)`}}/>
        <text className="title large" x="0" dy="0" textAnchor="middle" style={{transform: `translate(0,${0}px)`}}>{props.list.length}</text>
        <text className="sub-title" x="0" y="0" textAnchor="middle" style={{transform: `translate(0,${0}px)`}}>
          <tspan x="0" y="0" dy="1.8em">Add on</tspan>
          <tspan x="0" y="0" dy="2.8em">Companies</tspan>
        </text>
        
      </svg>
    )
  }
  else{
    return null
  }
}

export default AddOnCompanies