import * as THREE from 'three'
import VistaColors from '../Colors.js'
import axios from "axios"
import * as topojson from 'topojson'
// import worldMap from '../../data/world_map.json'

import {wireframe, geodecoder} from './Geographer'

const DEBUG = !true


const loader = new THREE.ObjectLoader( )




/**
  * @class Earth
  */
export default class Earth {
  /**
    * @constructor {object}
    */
  constructor ( {radius, origin = new THREE.Vector3() , color } ) {

    this.earth = new THREE.Group()


    this.radius = radius;

    if( !window.BrowserDetect.mobile ){
      this.sphereGeometry = new THREE.SphereGeometry(radius, 400, 400 )
    }else{
      this.sphereGeometry = new THREE.SphereGeometry(radius, 100, 100 )
    }
    this.sphereMaterial = new THREE.MeshLambertMaterial( {  color , transparent: false, opacity: 1} )
    this.outlineMaterial = new THREE.MeshLambertMaterial( { color:VistaColors.grays.Level_3 , transparent: true, opacity: 0.2, side: THREE.BackSide } )

    // sphere needs to stay ( for now ) independent from earth otherwise lats get messed up
    this.sphere = new THREE.Mesh( this.sphereGeometry, this.sphereMaterial )
    this.sphere.position.set( origin.x, origin.y, origin.z )
    this.sphere.name ="globeSphere"

    this.outline = new THREE.Mesh( this.sphereGeometry, this.outlineMaterial )
    this.outline.position.set( origin.x, origin.y, origin.z )
    this.outline.name ="globeOutline"
    this.outline.scale.multiplyScalar(1.05)
    // this.earth.add(this.outline)

    this.earth.position.set( origin.x, origin.y, origin.z )
    this.earth.name = "earth"
    this.earth.rotateX( -Math.PI/2)

    this.maps = {}

    this.worldMapTextures = {
      NEAR: null,
      MID: null,
      FAR: null,
      DEFAULT: null,
    }
    if ( DEBUG ) console.log("Earth ", radius, origin )


    axios.get(`http://${window.location.hostname}/data/topos_countries.json`).then( loadMap.bind(this) )
    axios.get(`http://${window.location.hostname}/data/topos_usa_states.json`).then( loadMap.bind(this) )
    // axios.get(`http://${window.location.hostname}/wp-json/globeInteractive/v1/map/countries`).then( loadMap.bind(this) )
    // axios.get(`http://${window.location.hostname}/wp-json/globeInteractive/v1/map/usa_states`).then( loadMap.bind(this) )

    // Mobile optimization : load only a lightweight json dataset for texture (Texture_MOBILE.json)
    if(window.BrowserDetect.mobile){

      // axios.get(`http://${window.location.hostname}/wp-json/globeInteractive/v1/texture/MOBILE`).then( loadTextureMobile.bind(this) )
      axios.get(`http://${window.location.hostname}/data/Texture_MOBILE.json`).then( loadTextureMobile.bind(this) )

    } else {
      Object.keys(this.worldMapTextures).map(key=>{
        axios.get(`http://${window.location.hostname}/data/Texture_${key}.json`).then( loadTexture.bind(this) )
        // axios.get(`http://${window.location.hostname}/wp-json/globeInteractive/v1/texture/${key}`).then( loadTexture.bind(this) )
      })

    }
  }
}

/**
  * axios returns a singleresponse object with the texture for mobile
  * all zoom levels set to same texture
  * @method loadTextureMobile
  * @param {object} res - response from ajax call
  * ref: https://threejs.org/docs/#api/en/loaders/ObjectLoader
  */
const loadTextureMobile = function (res) {

  if( res.data ) {

    // const textureData = JSON.parse( res.data );
    const textureData = res.data
    const texture = loader.parse( textureData );

    for(let key in this.worldMapTextures) {
      // set all to mobile texture (based on FAR json dataset)
      this.worldMapTextures[key] = texture;
      this.earth.add( this.worldMapTextures[key] );

      if(key == 'DEFAULT') texture.visible = true;
      console.log('key = ', key);
    }

  }
}

/**
  * axios returns a response object with the texture
  * @method loadTexture
  * @param {object} res - response from ajax call
  * ref: https://threejs.org/docs/#api/en/loaders/ObjectLoader
  */
const loadTexture = function( res ) {

  if( res.data ) {

    // const textureData = JSON.parse( res.data )
    const textureData = res.data
    const texture = loader.parse( textureData )

    this.worldMapTextures[texture.name] = texture;
    if ( texture.name === "DEFAULT" ) texture.visible = true;
    this.earth.add( this.worldMapTextures[texture.name] );
    if ( DEBUG ) console.log("Earth.loadTexture:", texture.name );
  }

}
/**
  * axios returns a response object with the map
  * @method loadMap
  * @param {object} res - response from ajax call
  */
const loadMap = function( res ) {
  console.log("loadMap", res )
  // console.log("loadMap", res.data )
  // console.log("loadMap", JSON.parse(res.data))
  if ( res.data ){
    // const data = JSON.parse(res.data).data
    const data = res.data.data

    for (var geoScale in data.objects) {
      if (data.objects.hasOwnProperty(geoScale) && geoScale !== "type") {
        if ( DEBUG ) console.log(`Earth.${geoScale}`, data, this);

        this.maps[geoScale] = {};
        const countriesMaterials = new THREE.MeshBasicMaterial( { color: VistaColors.grays.Level_1 , transparent: true, opacity: 0.3} )
        this.maps[geoScale].mesh = wireframe(topojson.mesh(data, data.objects[geoScale]), countriesMaterials , this.radius+0.5)

        this.earth.add(this.maps[geoScale].mesh);

        this.maps[geoScale].memo =  geodecoder(topojson.feature(data, data.objects[geoScale]).features);

      }
    }
  }
}


