/**
* GTM - Google Tag Manager
*
* for Vista AGM Interactive Globe
*
* Google Tag Manager - Custom event handlers for variables passed to analytics
* Import this into any module needed to track events; then call the main
* delegate function : fireCustomGoogleTagEvent(eventId {number});
*
*/

let lastCompany = ''
export function fireCustomGoogleTagEvent(eventId, data) {
  
  let eventName = "";

  // TODO: change event names to reflect what is being tracked

  switch (eventId) {
    case 0:
      eventName = "event 0";
      let justNames = []
      for (let i = 0; i < data.length; i++) {
        justNames[i] = data[i].name
      }
      window.dataLayer.push( { "Location Selection" : justNames} );
      break;
    case 1:
        lastCompany = data
      window.dataLayer.push( { "Company page selection" : data} );
      break;
      case 2:
        data =`${lastCompany}>${data}`
        window.dataLayer.push( { "Add on companies" : data} );
      break;
    case 3:
        data =`${lastCompany}>${data}`
      window.dataLayer.push( { "Other locations" : data} );
      break;
    default:
      break;
  }

  console.log("------------------------------------------------------------");
  console.log('event : ', eventId, data);

  
}
export const gtmevents = {
  'Location Selection':0,
  'Company page selection':1,
  'Add on companies': 2,
  'Other locations':3,
}
